const INITIAL_STATE = {
  transfers: [],
  currTransfer: null,
};

export function transferReducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "SET_TRANSFERS":
      return {
        ...state,
        transfers: action.transfers,
      };
    case "SET_TRANSFER":
      return {
        ...state,
        currTransfer: action.transfer,
      };
    case "ADD_TRANSFER":
      return {
        ...state,
        transfers: [...state.transfers, action.transfer],
      };
    case "REMOVE_TRANSFER":
      return {
        ...state,
        transfers: state.transfers.filter(
          (transfer) => transfer._id !== action.transferId
        ),
      };
    case "UPDATE_TRANSFER":
      const { updatedTransfer } = action;
      return {
        ...state,
        transfers: state.transfers.map((transfer) =>
          transfer._id === updatedTransfer._id ? updatedTransfer : transfer
        ),
      };
    default:
      return state;
  }
}
