import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import NiceButton from "../NiceButton/NiceButton";
import bitcoinGif from "../../assets/icons/bitcoin.gif";
import { useSelector } from "react-redux";
import { useHistory } from "react-router";
import { Avatar } from "@mui/material";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  // width: 400,
  bgcolor: "#B8C0C6",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
  overflow: "auto",
  maxHeight: "80vh",
};

export const ModalContacts = () => {
  const loggedUser = useSelector((state) => state.userReducer.user);
  const history = useHistory();
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const IconBitcoin = () => <img src={bitcoinGif} alt="" />;

  const onGoContact = (contactId) => {
    handleClose();
    history.push(`/contact/${contactId}`);
  };

  const contactDiv = {
    display: "flex",
    flexDirection: "row",
    gap: "20px",
    alignItems: "center",
    border: "1px solid black",
    padding: "10px",
    bgcolor: "#CEF6FF",
    cursor: "pointer",
    borderRadius:"10px"
  };

  return (
    <>
      <NiceButton
        onClick={handleOpen}
        Icon={IconBitcoin}
        className="bitcoin-gif"
      />
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <Typography variant="h2">Select Contact</Typography>

          {loggedUser.contacts.map((contact) => (
            <Box
              key={contact._id}
              sx={contactDiv}
              onClick={() => onGoContact(contact._id)}
            >
              <Avatar alt="" src={contact.contactImg} />
              <Typography>{contact.contactName}</Typography>
            </Box>
          ))}
        </Box>
      </Modal>
    </>
  );
};
